var assign = require('object-assign');


module.exports = function createMSDFShader (opt) {
  opt = opt || {};
  var opacity = typeof opt.opacity === 'number' ? opt.opacity : 1;
  var alphaTest = typeof opt.alphaTest === 'number' ? opt.alphaTest : 0.0001;
  var precision = opt.precision || 'highp';
  var color = opt.color;
  var map = opt.map;
  var map2 = opt.map2;
  var negate = typeof opt.negate === 'boolean' ? opt.negate : true;
  var time = opt.time;

  // remove to satisfy r73
  delete opt.map;
  delete opt.color;
  delete opt.precision;
  delete opt.opacity;
  delete opt.negate;

  return assign({
    uniforms: {
      opacity: { type: 'f', value: opacity },
      time: { type: 'f', value: time },
      viewport: { type: 'v2', value: new THREE.Vector2(window.innerWidth,window.innerHeight) },
      uMouse : { type: 'vec2', value: new THREE.Vector2(0,0) }, 
      map: { type: 't', value: map || new THREE.Texture() },
      map2 : {type: 't', value: map2 || new THREE.texture()},
      color: { type: 'c', value: new THREE.Color(color) }
    },
    vertexShader: `#version 300 es
     #define attribute in
     #define varying out
     #define texture2D texture
     precision highp float;
     precision highp int;
     #define HIGH_PRECISION
     #define SHADER_NAME ShaderMaterial
     #define VERTEX_TEXTURES
      attribute vec2 uv;
      attribute vec4 position;
      uniform mat4 projectionMatrix;
      uniform mat4 modelViewMatrix;
      varying vec2 vUv;
      void main() {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * position;
      }`,
    fragmentShader: `#version 300 es
      #define varying in
      out highp vec4 pc_fragColor;
      #define gl_FragColor pc_fragColor
      #define gl_FragDepthEXT gl_FragDepth
      #define texture2D texture
      #define textureCube texture
      #define texture2DProj textureProj
      #define texture2DLodEXT textureLod
      #define texture2DProjLodEXT textureProjLod
      #define textureCubeLodEXT textureLod
      #define texture2DGradEXT textureGrad
      #define texture2DProjGradEXT textureProjGrad
      #define textureCubeGradEXT textureGrad
      precision highp float;
      precision highp int;

      uniform float time;
      uniform float opacity;
      uniform vec2 uMouse;
      uniform vec2 viewport;
      uniform vec3 color;
      uniform sampler2D map;
      uniform sampler2D map2;
      varying vec2 vUv;

      float median(float r, float g, float b) {
        return max(min(r, g), min(max(r, g), b));
      }

      float createCircle() {
          vec2 viewportUv = gl_FragCoord.xy / viewport;
          float viewportAspect = viewport.x / viewport.y;

          vec2 mousePoint = vec2(uMouse.x, 1.0 - uMouse.y);
          float circleRadius = max(0.0, 100. / viewport.x) ;

          vec2 shapeUv = viewportUv - mousePoint;
          shapeUv /= vec2(1.0, viewportAspect);
          shapeUv += mousePoint;

          float dist = distance(shapeUv, mousePoint);
          dist = smoothstep(circleRadius, circleRadius + 0.001, dist);
          return dist;
          // return uMouse.y;
      }

      float getA(float a){
    
          float epsilon = 0.00001;
          float min_param_a = 0.0 + epsilon;
          float max_param_a = 1.0 - epsilon;
          
          return max(min_param_a, min(max_param_a, a));
      }

      float expEasing(float x, float a){
    
          a = getA(a);
          
          float y = 0.0;
          if(a < 0.5){
              // emphasis
            a = 2.0 * a;
              y = pow(x, a);
          } else {
              // de-emphasis
              a = 2.0 * (a - 0.5);
              y = pow(x, 1.0 / (1.0 - a));
          }
          
          return y;
      }

      void main() {
        float transparency = 0.;
        float fillTrans = 0.;
        float end;
        float circle = createCircle();
        float width = 0.1;
        float lineProgress = smoothstep(0.,1.,time/2.);;

        vec3 mysample = texture2D(map, vUv).rgb;
        float gr = texture2D(map2, vUv).r;
        float sigDist = median(mysample.r, mysample.g, mysample.b) - 0.5;
        float fill = clamp(sigDist/fwidth(sigDist) + 0.5, 0.0, 1.0);

        float border = fwidth(sigDist);
        float outline = smoothstep(0.0, border, sigDist);
        outline *= 1. - smoothstep(width - border, width, sigDist);

        float grgr = fract(2.*gr + time *.2);
        float start = smoothstep(0., 0.01 ,grgr);
        
        if(lineProgress >= .9)
        {
          fillTrans = expEasing(time/2.4,0.96);
          if(fillTrans >= 0.8)
          {
            end = smoothstep(0.3,0.3 - 0.01 ,grgr);
          }
          else
          {
            end = smoothstep(lineProgress,lineProgress - 0.01,grgr);
          }
          fill *= fillTrans;
        }
        else
        {
          fill = 0.;
          end = smoothstep(lineProgress,lineProgress - 0.01 ,grgr);
        }
        float mask = max(0.2,start*end);

        transparency = smoothstep(0.,1.,time/2.);
        float finalAlpha = (outline * mask + fill * circle) * transparency;

        gl_FragColor = vec4(color.xyz, finalAlpha);
     if (gl_FragColor.a < 0.001) discard;
      }
    `
  }, opt);
};
