function addText() {
    console.log(textTexture)

    textGeometry = createGeometry({
        text: 'LITWIZ LABS',
        font: font,
        align: 'center',
        flipY: true,
      });

    textMaterial = new THREE.RawShaderMaterial(MSDFShader({
    map: textTexture,
    map2: gradientMap,
    transparent: true,
    color: 0x4C4D9E,
    side: THREE.DoubleSide,
    }))

    let layout = textGeometry.layout
    let text = new THREE.Mesh(textGeometry, textMaterial)
    text.scale.set(0.01, -0.01, 0.01, 0.01);
    text.position.set(-0.01*layout.width/2, -0.01*layout.height/2, 0)
    scene.add(text);
    // text.position.set(0, -layout.descender + layout.height, 0)
    // text.scale.multiplyScalar(Math.random() * 0.5 + 0.5)
}

const normalTexture = textureLoader.load('/textures/NormalMap.png')

// Debug
const gui = new dat.GUI()

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()
// scene.background = 0xffffff;

//Particles
const particlesGeometry = new THREE.BufferGeometry;
const particlesCount = 5000;
const particleSpace = 5

const posArray = new Float32Array(particlesCount * 3)

for(let i = 0; i < particlesCount * 3; i++) {
    posArray[i] = (Math.random() - 0.5) * particleSpace
}

particlesGeometry.setAttribute('position', new THREE.BufferAttribute(posArray, 3))

// Objects
const geometry = new THREE.RingGeometry(0.7, 1, 120, 5, 0, 6.3)
const childGeometry = new THREE.TorusGeometry(0.6,0.2,2,100,6.284)

// Materials

const sphereMaterial = new THREE.PointsMaterial({
    size: 0.01,
    color: 0x4C4D9E
})

const childSphereMaterial = new THREE.MeshBasicMaterial({
    depthTest: true,
    wireframe: true,
    transparent: true,
    opacity: 0.1,
    color: 0x4C4D9E
})

const particleMaterial = new THREE.PointsMaterial({
    size: 0.005,
    color: 0x4C4D9E
})

// Mesh
const sphere = new THREE.Points(geometry,sphereMaterial);
const childSphere = new THREE.Mesh(childGeometry,childSphereMaterial);
sphere.add(childSphere);
childSphere.position.setZ(-1.3);
const particle = new THREE.Points(particlesGeometry, particleMaterial);
scene.add(sphere, particle)

// Lights

const pointLight = new THREE.PointLight(0xffffff, 0.1)
pointLight.position.x = 0
pointLight.position.y = 0
pointLight.position.z = 10
pointLight.intensity = 1

scene.add(pointLight)

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 0
camera.position.y = 0
camera.position.z = 2
scene.add(camera)

// Controls
// const controls = new OrbitControls(camera, canvas)
// controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    alpha: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

const pointLightHelper = new THREE.PointLightHelper( pointLight, 1 );
scene.add(pointLightHelper);



const light1 = gui.addFolder('Light 1')

light1.add(pointLight.position, 'x').min(-50).max(50).step(0.1);
light1.add(pointLight.position, 'y').min(-50).max(50).step(0.1);
light1.add(pointLight.position, 'z').min(-50).max(50).step(0.1);
light1.add(pointLight, 'intensity').min(0).max(100);

const light1Color = {
    color: 0xff0000
}

light1.addColor(light1Color, 'color').onChange(()=>{
    pointLight.color.set(light1Color.color)
})
var options = {
    save: function() {
        const saveObj = gui.getSaveObject()

        const text = JSON.stringify(saveObj);
        const name = "settings.json";
        const type = "text/plain";

        const a = document.createElement("a");
        const file = new Blob([text], { type: type });
        a.href = URL.createObjectURL(file);
        a.download = name;
        document.body.appendChild(a);
        a.click();
        a.remove();
    }
}

const saveState = gui.addFolder("Save Here")
saveState.add(options, 'save')


/**
 * Animate
 */


let lookLimitX = 0
let lookLimitY = 0

let moveLimitX = 0
let moveLimitY = 0

let mouseX = 0
let mouseY = 0


let lookMaxRange = 1
let moveMaxRange = 1
let lookEaseMult = 0.005
let moveEaseMult = 0.005
let particleSpeed = 0.00005

let lookTarget = new THREE.Vector3()
let moveTarget = new THREE.Vector3()

const windowHalfX = window.innerWidth / 2
const windowHalfY = window.innerHeight / 2

const lookFactorX = lookMaxRange / windowHalfX
const lookFactorY = lookMaxRange / windowHalfY

const moveFactorX = moveMaxRange / windowHalfX
const moveFactorY = moveMaxRange / windowHalfY

function value_limit(val, min, max) {
    return val < min ? min : (val > max ? max : val);
}

let inertia = 0
let onDocumentMouseMove = (event) => {
    //alert(event.clientX+","+event.clientY);
    mouseX = (event.clientX - windowHalfX);
    mouseY = (event.clientY - windowHalfY);
    
    textMaterial.uniforms.uMouse.value = new THREE.Vector2(event.clientX/window.innerWidth, event.clientY/window.innerHeight);

    lookLimitX = mouseX * lookFactorX;
    lookLimitY = mouseY * lookFactorY;

    moveLimitX = mouseX * moveFactorX;
    moveLimitY = mouseY * moveFactorY;

    inertia = 1
    // moveLimitY = value_limit(mouseY * moveFactorY,-0.4,0.4);

}

document.addEventListener('mousemove', onDocumentMouseMove)


const clock = new THREE.Clock()
let prevTime = +new Date()
let inertiaIncrement = 0.001


const tick = () =>
{

    let timeDiff = +new Date() - prevTime
    inertia += inertiaIncrement

    const elapsedTime = clock.getElapsedTime();
    if(textMaterial) {
        textMaterial.uniforms.time.value = elapsedTime;
    }
    childSphere.rotation.z += 0.0003

    // sphere.rotateOnAxis(new THREE.Vector3(0,0,3), 30)

    lookTarget.x += ( lookLimitX - lookTarget.x ) * lookEaseMult * timeDiff;
    lookTarget.y += ( - lookLimitY - lookTarget.y ) * lookEaseMult * timeDiff;

    
    lookTarget.z = camera.position.z; // assuming the camera is located at ( 0, 0, z );
    
    moveTarget.x += ( moveLimitX - moveTarget.x ) * moveEaseMult * timeDiff;
    moveTarget.y += ( - moveLimitY - moveTarget.y ) * moveEaseMult * timeDiff;
    moveTarget.z = camera.position.z; // assuming the camera is located at ( 0, 0, z );
    
    prevTime = +new Date()
    sphere.lookAt(lookTarget)

    particle.rotation.y = -mouseX * particleSpeed
    particle.rotation.x = -mouseY * particleSpeed
    
    particle.rotation.y *= inertia
    particle.rotation.x *= inertia

    childSphere.position.x =  moveTarget.x;
    childSphere.position.y = moveTarget.y;


    // Update Orbital Controls
    // controls.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()